.page-header {
	height: 18.75rem;
	position: relative;

	&::before {
		position: absolute;
		content: '';
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(to top, rgba(0, 0, 0, .7) 0%, rgba(255, 255, 255, 0) 100%);
	}

	@include phones {
		display: none;
	}

	&__title {
		position: absolute;
		top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
		font-size: 4rem;
		color: $white;
		margin: 0;
	}
}